import React from 'react';

type Variant = 'primary' | 'secondary' | 'outline' | 'destructive';
type Size = 'sm' | 'md' | 'lg';

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: Variant;
  size?: Size;
  className?: string;
}

const VARIANT_CLASSES: Record<Variant, string> = {
  primary: 'btn-primary',
  secondary: 'btn-secondary',
  outline: 'border border-slate-300 text-slate-800 bg-white hover:bg-slate-50',
  destructive: 'bg-red-600 text-white hover:bg-red-700',
};

const SIZE_CLASSES: Record<Size, string> = {
  sm: 'px-3 py-1.5 text-sm',
  md: 'px-4 py-2 text-base',
  lg: 'px-6 py-3 text-lg',
};

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(function Button(
  { variant = 'primary', size = 'md', className = '', children, ...props }: ButtonProps,
  ref
) {
  const classes = ['btn', VARIANT_CLASSES[variant], SIZE_CLASSES[size], className].filter(Boolean).join(' ');
  const isDisabled = Boolean((props as any).disabled);

  return (
    <button
      ref={ref}
      type={(props && props.type) || 'button'}
      className={classes}
      aria-disabled={isDisabled || undefined}
      {...props}
    >
      {children}
    </button>
  );
});

export default Button;
