'use client';

import { useState, ReactNode, useEffect, useRef } from 'react';
import Link from 'next/link';
import { ToastProvider } from './ToastContext';
import ToastView from './ToastView';
import Button from './Button';
import { usePathname, useRouter } from 'next/navigation';

interface LayoutProps {
  children: ReactNode;
}

type AuthUser = {
  id: string;
  role: 'patient' | 'doctor';
  full_name: string;
  email: string;
  phone?: string;
};

export default function Layout({ children }: LayoutProps) {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [user, setUser] = useState<AuthUser | null>(null);
  const [mounted, setMounted] = useState(false);
  const pathname = usePathname();
  const router = useRouter();

  // Fetch current user from /api/auth/me (cookie-based auth)
  useEffect(() => {
    let cancelled = false;

    async function fetchUser() {
      try {
        const res = await fetch('/api/auth/me', {
          method: 'GET',
          credentials: 'include',
        });

        if (!res.ok) {
          // 401 etc. ‚Äì treat as not logged in
          if (!cancelled) {
            setUser(null);
          }
          return;
        }

        const data = await res.json();
        if (!cancelled && data.success && data.user) {
          setUser(data.user);
        }
      } catch {
        if (!cancelled) setUser(null);
      } finally {
        if (!cancelled) setMounted(true);
      }
    }

    fetchUser();

    return () => {
      cancelled = true;
    };
  }, []);

  const isActive = (path: string) => pathname === path;

  const handleLogout = async () => {
    try {
      // Clear cookie on the server
      await fetch('/api/auth/logout', {
        method: 'POST',
        credentials: 'include',
      });

      // Clean up old localStorage data (backwards compatibility)
      if (typeof window !== 'undefined') {
        try {
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          localStorage.removeItem('userRole');
        } catch {
          // ignore
        }
      }
    } catch (e) {
      console.error('Logout error', e);
    } finally {
      setUser(null);
      setMobileMenuOpen(false);
      router.push('/login'); // or "/" if you prefer
    }
  };

  const toggleButtonRef = useRef<HTMLButtonElement | null>(null);
  const firstMobileLinkRef = useRef<HTMLAnchorElement | null>(null);

  useEffect(() => {
    if (mobileMenuOpen) {
      // move focus into the mobile menu
      setTimeout(() => firstMobileLinkRef.current?.focus(), 50);
    } else {
      // restore focus to the toggle
      toggleButtonRef.current?.focus();
    }
  }, [mobileMenuOpen]);

  const handleMobileKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Escape') {
      setMobileMenuOpen(false);
    }
  };

  // Basic focus trap: keep Tab/Shift+Tab cycling inside the mobile menu when open
  const handleMobileMenuKey = (e: React.KeyboardEvent) => {
    if (!mobileMenuOpen) return;
    if (e.key !== 'Tab') return;

    const focusable = Array.from(
      document.querySelectorAll('#mobile-menu a, #mobile-menu button')
    ) as HTMLElement[];
    if (!focusable.length) return;
    const first = focusable[0];
    const last = focusable[focusable.length - 1];

    if (!e.shiftKey && document.activeElement === last) {
      e.preventDefault();
      first.focus();
    }

    if (e.shiftKey && document.activeElement === first) {
      e.preventDefault();
      last.focus();
    }
  };

  return (
    <ToastProvider>
      <div className="flex flex-col min-h-screen bg-white">
        <header className="site-header sticky top-0 z-50 bg-white/80 backdrop-blur-md border-b">
          <div className="site-container h-16 flex items-center justify-between relative">
            <div className="flex items-center gap-3">
              <Link href="/" className="flex items-center gap-3">
                <div className="w-10 h-10 rounded-lg bg-linear-to-br from-indigo-600 to-blue-600 flex items-center justify-center text-white font-bold text-lg">
                  üè•
                </div>
                <span className="font-bold text-xl text-slate-900 hidden sm:inline">
                  ClinicHub
                </span>
              </Link>
            </div>

            <nav
              aria-label="Main navigation"
              className="hidden lg:flex items-center gap-6"
            >
              <Link
                href="/"
                className={`nav-link ${
                  isActive('/')
                    ? 'text-indigo-600'
                    : 'text-slate-700 hover:text-indigo-600'
                }`}
              >
                Home
              </Link>
              <Link
                href="/clinics"
                className={`nav-link ${
                  isActive('/clinics')
                    ? 'text-indigo-600'
                    : 'text-slate-700 hover:text-indigo-600'
                }`}
              >
                Clinics
              </Link>
              <Link
                href="/doctors"
                className={`nav-link ${
                  isActive('/doctors')
                    ? 'text-indigo-600'
                    : 'text-slate-700 hover:text-indigo-600'
                }`}
              >
                Doctors
              </Link>
            </nav>

            <div className="flex items-center gap-4">
              {mounted && user ? (
                <div className="flex items-center gap-3">
                  <div className="hidden sm:block text-sm">
                    <div className="font-semibold text-slate-900">
                      {user.full_name?.split(' ')[0]}
                    </div>
                  </div>
                  <Button
                    onClick={handleLogout}
                    className="hidden sm:inline-block"
                    variant="secondary"
                  >
                    Logout
                  </Button>
                </div>
              ) : mounted ? (
                <div className="hidden sm:flex items-center gap-2">
                  <Link href="/login">
                    <Button variant="outline" size="md">
                      Login
                    </Button>
                  </Link>
                  <Link href="/register">
                    <Button variant="primary" size="md">
                      Sign Up
                    </Button>
                  </Link>
                </div>
              ) : null}

              <button
                aria-label="Toggle menu"
                aria-expanded={mobileMenuOpen}
                aria-controls="mobile-menu"
                ref={toggleButtonRef}
                onClick={() => setMobileMenuOpen((s) => !s)}
                className="lg:hidden p-2 rounded-lg hover:bg-slate-100"
              >
                {mobileMenuOpen ? '‚úï' : '‚ò∞'}
              </button>
            </div>
          </div>

          {mobileMenuOpen && (
            <div
              id="mobile-menu"
              onKeyDown={(e) => {
                handleMobileKeyDown(e);
                handleMobileMenuKey(e);
              }}
              className="lg:hidden absolute inset-x-0 top-16 bg-white border-b shadow-lg"
            >
              <nav className="site-container py-4 flex flex-col gap-2">
                <Link
                  href="/"
                  ref={firstMobileLinkRef}
                  onClick={() => setMobileMenuOpen(false)}
                  className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium"
                >
                  Home
                </Link>
                <Link
                  href="/clinics"
                  onClick={() => setMobileMenuOpen(false)}
                  className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium"
                >
                  Clinics
                </Link>
                <Link
                  href="/doctors"
                  onClick={() => setMobileMenuOpen(false)}
                  className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium"
                >
                  Doctors
                </Link>
                <div className="border-t border-slate-200 pt-3 mt-3">
                  {mounted && user ? (
                    <Button
                      onClick={handleLogout}
                      className="w-full"
                      variant="destructive"
                    >
                      Logout
                    </Button>
                  ) : mounted ? (
                    <div className="flex gap-2">
                      <Link href="/login" className="flex-1">
                        <Button
                          onClick={() => setMobileMenuOpen(false)}
                          className="w-full"
                          variant="outline"
                        >
                          Login
                        </Button>
                      </Link>
                      <Link href="/register" className="flex-1">
                        <Button
                          onClick={() => setMobileMenuOpen(false)}
                          className="w-full"
                          variant="primary"
                        >
                          Sign Up
                        </Button>
                      </Link>
                    </div>
                  ) : null}
                </div>
              </nav>
            </div>
          )}
        </header>

        <main className="flex-1">{children}</main>

        <footer className="site-footer border-t">
          <div className="site-container py-12">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-6">
              <div>
                <div className="flex items-center gap-3 mb-3">
                  <div className="w-10 h-10 rounded-lg bg-linear-to-br from-indigo-600 to-blue-600 flex items-center justify-center text-white font-bold">
                    üè•
                  </div>
                  <div>
                    <div className="font-bold text-white">ClinicHub</div>
                    <div className="text-sm text-slate-300">
                      Trusted booking for modern healthcare
                    </div>
                  </div>
                </div>
                <div className="text-sm text-slate-300">
                  ¬© 2025 ClinicHub. All rights reserved.
                </div>
              </div>

              <div className="links">
                <Link href="/clinics">Clinics</Link>
                <Link href="/doctors">Doctors</Link>
                <Link href="/login">Login</Link>
                <Link href="/register">Sign up</Link>
              </div>
            </div>
          </div>
        </footer>

        <ToastView />
      </div>
    </ToastProvider>
  );
}
